<script>
/* eslint-disable */

import { api_url } from "../global_variable/global_variable.js";
import axios from "axios";
import sha256 from "js-sha256";

var XLSX = require("xlsx");

export default {
  name: "InputForm",

  components: {},

  data() {
    return {
      lastUpdate: new Date().toLocaleString(),
      loadingDialog: false,
      cancelDialog: false,
      flag: {
        campaign: true,
        whitelist: false,
        voucher_code: false,
      },
      textInputRules: [(v) => !!v || "Kolom harus diisi"],
      dateNow: new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
        .toISOString()
        .substr(0, 10),
      NoId: "AUTO GENERATED",
      menu1: false,
      menu2: false,
      menu3: false,
      menu4: false,
      menu5: false,
      menu6: false,
      campaign_form: {
        user_id: localStorage.nik,
        tipe_voucher: "Voucher",
        tipe_inject: "System (S)",
        campaign_title: "",
        campaign_subtitle: "",
        link_image: "",
        date1: new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
          .toISOString()
          .substr(0, 10),
        date2: new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
          .toISOString()
          .substr(0, 10),
        date3: new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
          .toISOString()
          .substr(0, 10),
        date4: new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
          .toISOString()
          .substr(0, 10),
        jenis_promo: "Targeted (T)",
        quota_voucher: "",
        bo_reward: "",
        juklak: "",
        skp: "",
        par: "",
        // par_finance: "",
        date5: new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
          .toISOString()
          .substr(0, 10),
        flag_inject_status: "Tidak",
        departemen: "",
        // date6: new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
        //   .toISOString()
        //   .substr(0, 10),
        mso_id: "",
      },
      search: "",
      dialog: {
        input: false,
        input2: false,
      },
      headers: [
        {
          text: "Brand ID",
          align: "start",
          value: "kd_merk",
          class: "black--text grey lighten-2",
        },
        {
          text: "Brand Name",
          value: "nama_merk",
          class: "black--text grey lighten-2",
        },
        {
          text: "Actions",
          value: "actions",
          class: "black--text grey lighten-2",
          sortable: false,
        },
      ],
      brand_data: [], //from glidev report.ms_merk_m
      brand_arr: [],
      input: {
        brand_id: "",
      },

      // voucher code
      mso_id_data: {},
      flag_mso_id: false,
      search2: "",
      dialog2: {
        input: false,
        input2: false,
      },
      headers2: [
        {
          text: "Voucher Code",
          align: "start",
          value: "voucher_code",
          class: "black--text grey lighten-2",
        },
        {
          text: "Member ID",
          align: "start",
          value: "member_id",
          class: "black--text grey lighten-2",
        },
        {
          text: "Blast Date",
          align: "start",
          value: "blast_date",
          class: "black--text grey lighten-2",
        },
        {
          text: "Actions",
          value: "actions",
          class: "black--text grey lighten-2",
          sortable: false,
        },
      ],
      campaign_data: [], //from parent
      voucher_data: [],
      input: {
        event: "",
        voucher_code: "",
        member_id: "",
        blast_date: "",
      },
    };
  },

  async mounted() {
    this.loadingDialog = true;

    // GET WHITELIST BRAND START
    try {
      var d = new Date();
      var m = d.getMonth() + 1;
      var t = d.getDate();
      if (m < 10) {
        m = "0" + m;
      }
      if (t < 10) {
        t = "0" + t;
      }
      var datestring = d.getFullYear() + "" + m + "" + t;
      var tgl = datestring;
      var kunci = "SSO" + "4lfag1ft" + tgl + "targeted_voucher";
      var token = sha256(kunci);
      // console.log(token);

      const headers = {
        "Access-Control-Allow-Origin": "*",
        "Access-Control-Allow-Credentials": "true",
        "Access-Control-Allow-Methods":
          "GET, PUT, POST, DELETE, OPTIONS, post, get",
        "Access-Control-Allow-Headers": "Origin, Content-Type, X-Auth-Token",
        "Content-Type": "application/json",
        "Content-Length": "<calculated when request is sent>",
        Host: "<calculated when request is sent>",
        Accept: "*/*",
        "Accept-Encoding": "gzip, deflate, br",
        Connection: "keep-alive",
        app: "sso",
      };

      await axios
        .get(api_url + "get-brand?token=" + token, headers)
        .then((response) => {
          // console.log(response.data);
          this.brand_data = response.data.data;
          if (this.brand_data.length == 0) {
            alert("Gagal mengambil data brand! Silahkan coba lagi.");
            this.loadingDialog = false;
            return;
          }
        })
        .catch((error) => {
          console.log("PushVoucher.vue (error) :: ", error);
          alert("Gagal mengambil data brand! Silahkan coba lagi.");
        });

      if (this.brand_data.length == 0) {
        this.loadingDialog = false;
        return;
      }
    } catch (e) {
      console.error(e);
      alert("Error: koneksi gagal");
      return;
    }
    // GET WHITELIST BRAND END

    this.loadingDialog = false;
  },

  methods: {
    btnFlag(card) {
      this.flag.campaign = false;
      this.flag.whitelist = false;
      this.flag.voucher_code = false;

      if (card == "campaign") {
        this.flag.campaign = true;
      } else if (card == "whitelist") {
        this.flag.whitelist = true;
      } else if (card == "voucher_code") {
        this.flag.voucher_code = true;
      }
    },
    getBtnColor(card) {
      if (card == "campaign") {
        if (this.flag.campaign == true) {
          return "#6861CE";
        } else {
          return "grey lighten-1";
        }
      } else if (card == "whitelist") {
        if (this.flag.whitelist == true) {
          return "#6861CE";
        } else {
          return "grey lighten-1";
        }
      } else if (card == "voucher_code") {
        if (this.flag.voucher_code == true) {
          return "#6861CE";
        } else {
          return "grey lighten-1";
        }
      }
    },
    flagBySystem() {
      if (this.campaign_form.tipe_inject == "System (S)") {
        return true;
      } else {
        return false;
      }
    },
    onAddWhitelist() {
      if (
        this.brand_data.find((c) => c.kd_merk == this.input.brand_id) ==
        undefined
      ) {
        alert("Gagal menambahkan item!");
        return;
      }
      if (
        this.brand_arr.filter((e) => e.kd_merk == this.input.brand_id).length >
        0
      ) {
        alert("Brand sudah di dalam tabel!");
        return;
      }
      var obj = this.brand_data.find((c) => c.kd_merk == this.input.brand_id);

      this.brand_arr.push(obj);

      this.input.brand_id = "";

      this.dialog.input = false;

      // console.log(this.brand_data);
    },
    onDeleteItem(item) {
      // console.log(item);

      this.brand_arr = this.brand_arr.filter(function (i) {
        return i.kd_merk !== item.kd_merk;
      });

      // console.log(this.brand_data);
      //   localStorage.setItem(
      //     "changed_arr_whitelist",
      //     JSON.stringify(this.brand_arr)
      //   );

      alert("Berhasil menghapus item!");
    },
    onFileChange(e) {
      var files = e.target.files || e.dataTransfer.files;
      if (!files.length) return;
      this.filetojson(files[0]);
    },
    filetojson(file) {
      var reader = new FileReader();
      var t = this;
      reader.onload = function (e) {
        var data = e.target.result;
