    dateIsValid(dateStr) {
      const regex = /^\d{4}-\d{2}-\d{2}$/;

      if (dateStr.match(regex) === null) {
        return false;
      }

      const date = new Date(dateStr);

      const timestamp = date.getTime();

      if (typeof timestamp !== "number" || Number.isNaN(timestamp)) {
        return false;
      }

      return date.toISOString().startsWith(dateStr);
    },
    addVoucherCode() {
      if (this.dateIsValid(this.input.blast_date)) {
        this.voucher_data.push({
          voucher_code: this.input.voucher_code,
          member_id: this.input.member_id,
          blast_date: this.input.blast_date,
        });
      } else {
        alert("Format tanggal blast voucher code salah!");
      }

      this.input.voucher_code = "";
      this.input.member_id = "";
      this.input.blast_date = "";
      this.dialog2.input = false;
    },
    onDeleteWhitelist(item) {
      // console.log(item);
      this.voucher_data = this.voucher_data.filter(function (i) {
        return i.voucher_code !== item.voucher_code;
      });
      alert("Berhasil menghapus item!");
    },
