        // console.log(jsondata);
        if (
          jsondata[0].voucher_code &&
          jsondata[0].member_id &&
          jsondata[0].blast_date
        ) {
          for (var i = 0; i < jsondata.length; i++) {
            // console.log(jsondata[i]);
            if (
              vm.voucher_data.filter(
                (e) => e.voucher_code == jsondata[i].voucher_code
              ).length > 0
            ) {
              alert("Brand sudah di dalam tabel!");
              return;
            } else if (vm.dateIsValid(jsondata[i].blast_date)) {
              vm.voucher_data.push(jsondata[i]);
            } else {
              vm.voucher_data = [];
              alert("Format tanggal blast voucher code salah!");
              return;
            }
          }
        } else {
          vm.voucher_data = [];
          alert("Format header xslx salah!");
          return;
        }
        // console.log(vm.voucher_data);
      };
      reader.readAsBinaryString(file);
    },
  },
};
</script>

